/* Streamdown  */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Helvetica Neue", "PingFang SC", "Hiragino Sans GB", "Noto Sans SC", "Microsoft YaHei", "", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-feature-settings: "kern" 1;
  text-rendering: optimizeLegibility;
  /* New background for Glassmorphism */
  background: linear-gradient(135deg, #1a1a2e, #16213e, #0f3460);
  background-size: 400% 400%;
  animation: gradientAnimation 15s ease infinite;
  color: var(--text-primary, #e0e0e0);
  height: 100vh;
  overflow: hidden;
  line-height: 1.4;
  letter-spacing: -0.01em;
  font-weight: 400;
  font-size: 14px;
  /*  */
  touch-action: manipulation;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
}

@keyframes gradientAnimation {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

#root {
  height: 100vh;
  display: flex;
  flex-direction: column;
  /*  */
  touch-action: manipulation;
  -webkit-touch-callout: none;
  -webkit-tap-highlight-color: transparent;
}

/*  */
* {
  -webkit-tap-highlight-color: transparent;
}

/*  */
button, 
[role="button"], 
.clickable,
.prompt-card,
.role-dropdown-trigger,
.role-option,
.menu-btn,
.send-button,
.stop-button,
.quick-response-btn,
.plus-button,
.timeline-toggle,
.settings-btn,
.theme-toggle-btn {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/*  */
.font-display-large {
  font-size: 48px;
  font-weight: 600;
  line-height: 1.1;
  letter-spacing: -0.02em;
}

.font-display-medium {
  font-size: 36px;
  font-weight: 600;
  line-height: 1.15;
  letter-spacing: -0.015em;
}

.font-display-small {
  font-size: 28px;
  font-weight: 600;
  line-height: 1.2;
  letter-spacing: -0.01em;
}

.font-headline-large {
  font-size: 24px;
  font-weight: 600;
  line-height: 1.25;
  letter-spacing: -0.01em;
}

.font-headline-medium {
  font-size: 20px;
  font-weight: 600;
  line-height: 1.3;
  letter-spacing: -0.005em;
}

.font-headline-small {
  font-size: 18px;
  font-weight: 600;
  line-height: 1.35;
  letter-spacing: 0em;
}

.font-title-large {
  font-size: 16px;
  font-weight: 600;
  line-height: 1.4;
  letter-spacing: 0em;
}

.font-title-medium {
  font-size: 14px;
  font-weight: 600;
  line-height: 1.4;
  letter-spacing: 0.005em;
}

.font-title-small {
  font-size: 13px;
  font-weight: 600;
  line-height: 1.4;
  letter-spacing: 0.01em;
}

.font-label-large {
  font-size: 13px;
  font-weight: 500;
  line-height: 1.4;
  letter-spacing: 0.01em;
}

.font-label-medium {
  font-size: 12px;
  font-weight: 500;
  line-height: 1.35;
  letter-spacing: 0.02em;
}

.font-label-small {
  font-size: 11px;
  font-weight: 500;
  line-height: 1.35;
  letter-spacing: 0.025em;
}

.font-body-large {
  font-size: 15px;
  font-weight: 400;
  line-height: 1.45;
  letter-spacing: 0em;
}

.font-body-medium {
  font-size: 14px;
  font-weight: 400;
  line-height: 1.4;
  letter-spacing: 0.005em;
}

.font-body-small {
  font-size: 12px;
  font-weight: 400;
  line-height: 1.35;
  letter-spacing: 0.01em;
}

/*  -  */
.font-mono {
  font-family: "SF Mono", Monaco, "Cascadia Code", "Roboto Mono", Consolas, "Courier New", monospace;
  font-feature-settings: "liga" 0, "calt" 0;
  font-variant-ligatures: none;
  font-size: 13px;
  font-weight: 400;
  line-height: 1.35;
  letter-spacing: 0em;
}

/*  */
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

/*  -  */
:lang(zh) {
  letter-spacing: -0.005em;
  line-height: 1.35;
}

/*  -  */
:lang(en) {
  letter-spacing: -0.01em;
  line-height: 1.4;
}

/*  */
.font-tabular {
  font-variant-numeric: tabular-nums;
  font-feature-settings: "tnum" 1;
}

/*  */
strong, b {
  font-weight: 600;
  letter-spacing: -0.005em;
}

/* Markdown  */
table {
  border-collapse: collapse;
  width: 100%;
  margin: 16px 0;
  font-size: 14px;
  border: 1px solid var(--border-color);
  background: var(--bg-secondary);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

th, td {
  border: 1px solid var(--border-color);
  padding: 12px 16px;
  text-align: left;
  color: var(--text-primary);
}

th {
  background: var(--bg-tertiary);
  font-weight: 600;
  color: var(--text-primary);
  font-size: 14px;
}

tr:nth-child(even) {
  background: var(--bg-hover);
}

tr:hover {
  background: var(--bg-secondary);
}

/*  */
@media (max-width: 768px) {
  table {
    font-size: 12px;
  }
  
  th, td {
    padding: 8px 12px;
  }
}

/*  */
@media (max-width: 768px) {
  /*  */
  ::-webkit-scrollbar {
    width: 0px;
    height: 0px;
    display: none;
  }
  
  /* Firefox  */
  * {
    scrollbar-width: none;
  }
  
  /*  */
  .chat-messages,
  .conversations-list,
  .code-content,
  .welcome-textarea,
  .message-textarea-clean,
  .dropdown-menu,
  .model-dropdown,
  .timeline-items,
  pre,
  code {
    overflow-y: auto !important;
    -webkit-overflow-scrolling: touch !important;
  }
}

/*  */
@media (hover: none) and (pointer: coarse) {
  /*  */
  ::-webkit-scrollbar {
    width: 4px;
    height: 4px;
  }
  
  ::-webkit-scrollbar-track {
    background: transparent;
  }
  
  ::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0.2);
    border-radius: 2px;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: rgba(0, 0, 0, 0.3);
  }
  
  /*  */
  [data-theme="dark"] ::-webkit-scrollbar-thumb {
    background: rgba(255, 255, 255, 0.2);
  }
  
  [data-theme="dark"] ::-webkit-scrollbar-thumb:hover {
    background: rgba(255, 255, 255, 0.3);
  }
}